.selected-div{
  display: flex;
  background-color: whitesmoke;
  width: 80%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
}

.selected-div-text{
  font-weight: bold;
  font-size: calc(16px + 0.1vw);
}


@media  (max-width: 1200px) {
  .selected-div{
    flex-direction: column;
  }
 }